@import '../../styles/colors.scss';
@import '../../styles/mixins.scss';

.travel-photo-section {
    gap: 100px;

    .travel-photo-section__header {
        gap: 30px;

        .travel-photo-section__header__subtitle {
            max-width: 418px;
        }
    }

    .travel-photo-section__content {
        gap: 30px;

        .travel-photo-section__photo-container {
            display: flex;
            flex-direction: row;
            justify-content: center;
            min-width: 100%;
            padding: 0px;
            gap: 30px;

            .travel-photo-section__big-photo {
                max-width: 23%;
                object-fit: cover;

                border-radius: 50px;
            }

            .travel-photo-section__small-photo {
                max-width: 18%;
                min-height: 301px;
                object-fit: cover;

                border-radius: 50px;
            }
        }
    }
}

@media (max-width: 1630px) {
    .travel-photo-section {

        .travel-photo-section__content {

            .travel-photo-section__photo-container {

                .travel-photo-section__big-photo {
                    min-height: 300px;
                    max-width: 26.7%;
                }
                .max-size {
                    display: none;
                }

                .travel-photo-section__small-photo {
                    max-width: 22.5%;
                }

            }
        }
    }
}

